            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;右眼:<template>
            <el-select v-model="rightDiopterValue" size="mini" clearable  placeholder="请选择" style="width: 160px">
