            <el-checkbox v-model="editFormJwsForm.DM_year">糖尿病</el-checkbox>
            <el-checkbox v-model="editFormJwsForm.HTN_year">高血压</el-checkbox>
            <el-checkbox v-model="editFormJwsForm.HPL_year">高血脂</el-checkbox>
            <!--                <span>糖尿病:</span><el-input-number v-model="editFormJwsForm.DM_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年-->
            <!--                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;-->
            <!--                <span>高血压:</span><el-input-number v-model="editFormJwsForm.HTN_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年-->
            <!--                <br/>-->
            <!--                <span>高血脂:</span><el-input-number v-model="editFormJwsForm.HPL_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年-->
            <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;妊娠:</span><el-input-number v-bind:disabled="pre_seen" v-model="editFormJwsForm.pregnant_weeks" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>周
            <br/>
