<script>
  import axios from 'axios'
  import {
    addPatient,
    checkPatient,
    clickDiagnoseButton,
    editPatientBasicInfo,
    get_patient_info,
    removePatient
  } from '../../api/api';
  import url from "../../static/js/hostUrl";
  import cookie from "../../static/js/cookie";
  import "../../static/js/jquery.form";

  let host = url.url;
  export default {
    name:'undiaWechat',
    /*created：在模板渲染成html前调用，即通常初始化某些属性值，然后再渲染成视图。*/
    created:function(){
      const hosType = localStorage.getItem('type');
      if(hosType==='1'){
        this.superHos = true;
        this.normalHos = false;
      }else{
        this.normalHos = true;
        this.superHos = false;
      }

      let page_info_we = this.$store.state.page_info_we;
      let p = page_info_we['currentPage'];
      let page_size = page_info_we['page_size'];
      this.currentPage = p;
      this.page_size = page_size;
      this.get_patients(p,page_size);

    },
    /*mounted：在模板渲染成html后调用，通常是初始化页面完成后，再对html的dom节点进行一些需要的操作。*/
    mounted:function(){

    },

    // watch:{                           /*采用watch方法，监听到hospital_id变化时再执行get_patients方法*/
    //   hospital_id(newVal,oldVal){
    //     console.log('oldVal',oldVal);
    //     console.log('newVal',newVal);
    //     let page_info = this.$store.state.page_info;
    //     let flag = this.$store.state.flag;
    //     if(flag){this.get_patients(1,10);return false};
    //
    //     if(page_info.page_size&&page_info.currentPage){
    //       this.page_size = page_info.page_size;
    //       this.currentPage = page_info.currentPage;
    //       this.get_patients(page_info.currentPage,page_info.page_size);
    //     }else{
    //       this.get_patients(1,10);
    //     }
    //   },
    //   immediate: true
    // },

    methods:{
      /*组件重新加载*/
      // reloadFun () {
      //   this.reload()
      // },
      /*刷新*/
      refresh:function () {
        $('#selectMenu').html('查看全部');
        this.payment = '';

        let page_info_we = this.$store.state.page_info_we;
        //
        // let p = page_info_we['currentPage'];
        let page_size = page_info_we['page_size'];
        let p = 1;
        this.currentPage = 1;
        //let page_size = 10;

        this.inputVal = '';
        this.dateValue = '';

        this.tableData = [];
        this.get_patients(p,page_size,'','');
      },

      /*向store.js传递page和size*/
      delieverPageAndSize:function(){
        let currentPage = this.currentPage;
        let page_size = this.page_size;
        let page_info_we = {};
        page_info_we['currentPage'] = currentPage;
        page_info_we['page_size'] = page_size;
        this.$store.commit('rememberWePageAndSize',page_info_we);
      },

      /*控制每页多少数量*/
      handleSizeChange( val) {
        let flag = false;
        this.$store.commit('changeFlag',flag);
        let page_size = val;
        this.page_size = page_size;
        this.currentPage = 1;
        let p = this.currentPage;
        let search = this.inputVal;
        this.delieverPageAndSize();
        console.log(p,page_size);
        if(this.otherDate){
          this.get_patients(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients(p,page_size,'',search);
          console.log("page_size total");
        }
      },
      /*控制第几页*/
      handleCurrentChange(val) {
        let flag = false;
        this.$store.commit('changeFlag',flag);
        let p  = val;
        let page_size = this.page_size;
        let search = this.inputVal;
        let payment = this.payment;
        this.delieverPageAndSize();
        if(this.otherDate){
          this.get_patients(p,page_size,this.dateValue,search,payment);
        }else{
          this.get_patients(p,page_size,'',search, payment);
        }
      },

      get_patients:function (p,size,apply_time,search,payment) {
        let para = {};
        para['doctor2'] = localStorage.getItem('user_id');
        para['p'] = p;
        para['page_size'] = size;
        para['apply_time'] = apply_time;
        para['search'] = search;
        para['ordering'] = '-apply_time';
        para['status'] = 0;
        para['payment'] = payment;
        get_patient_info(para).then(response=>{
          console.log('response',response);
          this.tableData = response['results'];
          this.totalNum = response['count'];
        })
        //诊断（如第二页最后一个患者时）完毕后，再返回第二页会出现错误，此处需要将页数减1
          .catch(error=>{
            console.log((error));
            p = p-1;
            axios.get(host+"/patients/?p="+p+"&page_size="+size+"&hospital_id="+hospital_id+"&ordering=-apply_time&status=0")
              .then(response=>{
                console.log('response',response);
                this.tableData = response['patient_list'];
                this.totalNum = response['count'];
              })
          })
      },

      get_patients_by_rules:function (p,size,apply_time,search) {
        let para = {};
        para['p'] = p;
        para['page_size'] = size;
        para['apply_time'] = apply_time;
        para['search'] = search;
        para['ordering'] = '-apply_time';
        para['status'] = 0;
        get_patient_info(para).then(response=>{
          console.log('response',response);
          this.tableData = response;
          this.totalNum = response.length;
        })
          .catch(error=>{
            console.log((error))
          })
      },
      selectDate:function () {
        this.otherDate = true;
        let seperator1 = "-";
        let date = new Date(this.dateValue);
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let strDate = date.getDate();
        let payment = this.payment;
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        let selectdate = year + seperator1 + month + seperator1 + strDate;
        this.dateValue  = selectdate;
        this.currentPage = 1;
        let search = this.inputVal;
        if(selectdate === '1970-01-01'){
          selectdate = '';
        }
        this.get_patients(1,this.page_size,selectdate,search,payment);
      },

      searchBtn:function () {
        let payment = this.payment;
        this.currentPage = 1;
        this.get_patients(1,this.page_size,this.dateValue,this.inputVal,payment);
      },
      //性别显示转换
      formatSex: function (row) {
        let patient_gender = row.patient_gender;
        return patient_gender === "0" ? '男' : patient_gender === "1" ? '女' : '未知';
      },
      //诊断状态显示转换
      formatStatus: function (row) {
        let status = row.status;
        return status === "0" ? '未诊断' : status === "2" ? '已诊断' : '诊断中';
      },
      //付费状态显示转换
