      get_file_num :function () {
        let fileReturnValue = [];
        let fileNum = document.getElementById("fileToUpload").files.length;
        let files = document.getElementById("fileToUpload").files;
        for (let i=0;i<files.length;i++){
          console.log('1024',files[i].size);
          if(files[i].size/1048576>15){
            fileReturnValue[0]=false;
            break
          }else{
            fileReturnValue[0]=true;
          }
        }

        fileReturnValue.push(fileNum);

        return fileReturnValue;
      },
      uploadPic:function() {
        // check_expired().then(response=>{
        //   console.log('response',response);
        // });
        const loading = this.$loading({
          lock: true,
          // text: 'Loading',
          text: 'AI智能识别左右眼中',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)',
        });
        setTimeout(() => {
          loading.setText('AI智能计算中');
        }, 3000);

        let num = this.get_file_num()[1];
        let fileSize = this.get_file_num()[0];
        if(num>2){
          this.$alert('单次上传照片数量不可超过两张', '温馨提示', {
            confirmButtonText: '确定',
            callback: action => {
              console.log(action)
            }
          });
          loading.close();
          return false;
        }else if(fileSize===false){
          this.$alert('单张照片大小不可超过10M', '温馨提示', {
            confirmButtonText: '确定',
            callback: action => {
              console.log(action)
            }
          });
          loading.close();
        }else {
          // 上传设置
          let options = {
            // 规定把请求发送到那个URL
            url: host+ "operation/web_upload_picture/",
            data:{'userName':cookie.getCookie('name'),
              'patient_id':this.upload_id,
              'diagnose_record':this.diagnose_record},
            // 请求方式
            type: "post",
            // 服务器响应的数据类型
            dataType:"json",
            cache: false,
            headers:{
              Authorization : localStorage.getItem('token')
            },
            processData: false,   // 告诉jQuery不要处理数据
            contentType: false,   // 告诉jQuery不要设置类型
            // 请求成功时执行的回调函数
            // error:function (result, status, xhr) {
            //    alert("session 过期");
            // }

            success: function(result, status, xhr) {
              console.log('result',result);
              if(result===0){
                loading.close();
                this.$alert('上传失败,仅支持jpg,png,jpeg,bmp格式', '温馨提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.log(action)
                  }
                });
              }else if(result===1){
                loading.close();
                this.$alert('上传失败', '温馨提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.log(action)
                  }
                });
              }else if(result===2){
                loading.close();
                let flag = true;
                this.$store.commit('changeFlag',flag);
                this.refresh();
                let business_type = localStorage.getItem('business_type');
                if(business_type === '1'){
                  this.sendSms(this.sms_patient_name, this.sms_patient_tel)  //发送短信
                }
                this.$alert('上传成功', '温馨提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.log(action)
                  }
                });
              }
              else{
                loading.close();
                this.$alert('上传失败', '温馨提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.log(action)
                  }
                });
              }
            }.bind(this),
            error:function () {
              loading.close();

              this.$alert('上传失败', '温馨提示', {
                confirmButtonText: '确定',
                callback: action => {
                  console.log(action)
                }
              });
            }.bind(this)
          };
          $("#jvForm").ajaxSubmit(options);
        }
      },

      //提交
      addSubmit: function () {
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            this.$confirm('确认提交吗？', '提示', {}).then(() => {
              this.addLoading = true;
              //NProgress.start();
              console.log('this.jwsForm',this.jwsForm);
              console.log(this.leftDiopterValue,this.rightDiopterValue);
              let jws= this.jwsForm;
              let submitInfo = {};
              if(this.leftDiopterValue!==''){
                jws['left_diopter'] = this.leftDiopterValue;
              }
              if(this.rightDiopterValue!==''){
                jws['right_diopter'] = this.rightDiopterValue;
              }
              submitInfo['jwsInfo'] = jws;
              let basicInfo = this.addForm;

              delete basicInfo['genderRadio'];
              delete basicInfo['paymentRadio'];
              delete basicInfo['jwsRadio'];
              submitInfo['basicInfo'] = basicInfo;
              let para = Object.assign({}, submitInfo);
              //para['basicInfo']['user_token'] = cookie.getCookie('token');
              // para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
              delete basicInfo['jwsVisible'];
              addPatient(para).then((res) => {
                this.addLoading = false;
                if(res===1){
                  this.$message({
                    message: '提交成功',
                    type: 'success'
                  });
                  this.$refs['addForm'].resetFields();
                  this.addFormVisible = false;
                  let flag = true;
                  this.$store.commit('changeFlag',flag);
                  this.refresh();
                }else{
                  this.$message({
                    message: '提交失败',
                    type: 'error'
                  });
                }

              });
            });
          }
        });
      },
