    data() {
      let validateTel = (rule, value, callback) => {
        let telLength = value.length;
        if (telLength !==11 ) {
          callback(new Error('手机号为11位'));
        } else {
          callback();
        }
      };

      let validateIDnumber = (rule, value, callback) => {
        if(value){
          let telLength = value.length;
          if (telLength !==18 ) {
            callback(new Error('中国大陆身份证号为18位'));
          } else {
            callback();
          }
        }else{
          callback();
        }
      };

      let validateName = (rule, value, callback) => {
        let nameLength = value.length;
        let reg = /^[\u4E00-\u9FA5A-Za-z0-9_]+$/;
        if (nameLength >10 ) {
          callback(new Error('姓名长度需要小于10个字'));
        }else if(!reg.test(value)){
          callback(new Error('禁止输入空格等特殊字符'));
        }
        else {
          callback();
        }

      };

      let validateAddress = (rule, value, callback) => {
        if(!value){
          callback();
        }
        let nameLength = value.length;
        if (nameLength >30 ) {
          callback(new Error('地址长度需要小于30个字'));
        } else {
          callback();
        }
      };

      let validateAge = (rule, value, callback) => {
        if (value<1) {
          callback(new Error('请填写正确年龄'));
        } else {
          callback();
        }
      };

